import React, { useState } from 'react';
import Navbar from './components/Navbar';
import DataTableComponent from './components/DataTableComponent';
import HelpModal from './components/HelpModal';
import Select from 'react-select';
import './index.css';

function App() {
  const [data1, setData1] = useState([]);
  const [data2, setData2] = useState([]);
  const [selectedSobjectNames, setSelectedSobjectNames] = useState([]);
  const [showHelp, setShowHelp] = useState(true);

  const handleFileLoad1 = (data) => {
    setData1(data);
  };

  const handleFileLoad2 = (data) => {
    setData2(data);
  };

  const allSobjectNames = [
    ...new Set([
      ...data1.map(item => item.sobjectName),
      ...data2.map(item => item.sobjectName)
    ])
  ];

  const sobjectOptions = allSobjectNames.map(name => ({ value: name, label: name }));

  const filteredData1 = data1.filter(item => 
    selectedSobjectNames.length === 0 || selectedSobjectNames.includes(item.sobjectName)
  );

  const filteredData2 = data2.filter(item => 
    selectedSobjectNames.length === 0 || selectedSobjectNames.includes(item.sobjectName)
  );

  const isRowInData2 = (row) => filteredData2.some(item => JSON.stringify(item) === JSON.stringify(row));
  const isRowInData1 = (row) => filteredData1.some(item => JSON.stringify(item) === JSON.stringify(row));

  const rowHighlight1 = [
    {
      when: row => !isRowInData2(row),
      style: { backgroundColor: '#f8d7da' },
    },
  ];

  const rowHighlight2 = [
    {
      when: row => !isRowInData1(row),
      style: { backgroundColor: '#d4edda' },
    },
  ];

  const handleSobjectChange = (selectedOptions) => {
    setSelectedSobjectNames(selectedOptions ? selectedOptions.map(option => option.value) : []);
  };

  return (
    <div className="App">
      <Navbar onHelpClick={() => setShowHelp(true)} />
      <div className="container mx-auto mt-4">
        <div className="mb-4">
          <label className="mr-2">Select sobjectName:</label>
          <Select
            isMulti
            options={sobjectOptions}
            className="basic-multi-select"
            classNamePrefix="select"
            onChange={handleSobjectChange}
          />
        </div>
        <div className="flex">
          <div className="w-1/2 p-2">
            <h2 className="text-xl mb-2">Org 1</h2>
            <DataTableComponent data={filteredData1} onFileLoad={handleFileLoad1} rowHighlight={rowHighlight1} />
          </div>
          <div className="w-1/2 p-2">
            <h2 className="text-xl mb-2">Org 2</h2>
            <DataTableComponent data={filteredData2} onFileLoad={handleFileLoad2} rowHighlight={rowHighlight2} />
          </div>
        </div>
        <footer className="bg-white-200 p-12 text-center">
        <hr></hr>
        Made in{" "}
        <a
          href="https://en.wikipedia.org/wiki/New_Hampshire"
          id="nh"
          target="_blank"
          rel="noopener noreferrer"
        >
          New Hampshire
        </a>{" "}
        with Love{" "}
        <span role="img" aria-label="heart">
          
        </span>{" "}
      </footer>
      </div>
      <HelpModal show={showHelp} onClose={() => setShowHelp(false)} />
    </div>
  );
}

export default App;
