import React from 'react';
import DataTable from 'react-data-table-component';
import { CSVLink } from 'react-csv';
import Papa from 'papaparse';

const columns = [
  { name: 'sobjectName', selector: row => row.sobjectName, sortable: true },
  { name: 'sobjectLabel', selector: row => row.sobjectLabel, sortable: true },
  { name: 'name', selector: row => row.name, sortable: true },
  { name: 'label', selector: row => row.label, sortable: true },
  { name: 'type', selector: row => row.type, sortable: true },
  { name: 'length', selector: row => row.length, sortable: true },
  { name: 'nillable', selector: row => row.nillable, sortable: true },
  { name: 'referenceTo', selector: row => row.referenceTo, sortable: true },
  { name: 'relationshipName', selector: row => row.relationshipName, sortable: true },
  { name: 'unique', selector: row => row.unique, sortable: true },
  { name: 'updateable', selector: row => row.updateable, sortable: true },
  { name: 'inlineHelpText', selector: row => row.inlineHelpText, sortable: true },
  { name: 'picklistValues', selector: row => row.picklistValues, sortable: true },
  { name: 'externalId', selector: row => row.externalId, sortable: true },
];

const DataTableComponent = ({ data, onFileLoad, rowHighlight }) => {
  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    if (file) {
      Papa.parse(file, {
        header: true,
        skipEmptyLines: true,
        complete: function (results) {
          onFileLoad(results.data);
        },
      });
    }
  };

  return (
    <div className="p-4">
      <input
        type="file"
        accept=".csv"
        onChange={handleFileUpload}
        className="mb-4"
      />
      {data.length > 0 && (
        <>
          <DataTable
            columns={columns}
            data={data}
            pagination
            highlightOnHover
            pointerOnHover
            conditionalRowStyles={rowHighlight}
          />
          <CSVLink
            data={data}
            filename={"table-data.csv"}
            className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded mt-4 inline-block"
          >
            Export to CSV
          </CSVLink>
        </>
      )}
    </div>
  );
};

export default DataTableComponent;
